@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap);
*{
  margin: 0;
}
.app{
    background-color: rgb(243, 243, 243);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    font-family: 'Poppins', sans-serif;
}


.app__body{
    display: -webkit-flex;
    display: flex;
    margin-top: 35px;
    max-width: 1200px;
    margin-left: 20px;
    margin-right: 20px;
    font-family: 'Poppins', sans-serif;
}
.header {
  display: -webkit-flex;
  display: flex;
  border-bottom: 0.1px solid rgb(196, 196, 196);
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
  padding: 10px 0 10px;
  width: 100%;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 999;
  background-color: white;
}
.header__left {
  display: -webkit-flex;
  display: flex;
}
.header__left > img {
  object-fit: contain;
  height: 40px;
  margin-right: 10px;
}

.header__search {
  padding: 10px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border-radius: 5px;
  height: 22px;
  color: gray;
  background-color: rgb(241, 221, 253);
}

.header__search > input {
  outline: none;
  border: none;
  background: none;
}
.header__right {
  display: -webkit-flex;
  display: flex;
}

.headerOption {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 20px;
  cursor: pointer;
  color: gray;
}

.headerOption:hover {
  color: black;
}

.headerOption__title {
  font-size: 12px;
  font-weight: 400;
}

.headerOption__icon {
  object-fit: contain;
  height: 25px !important;
  width: 25px !important;
}



.sidebar {
  top: 80px;
  position: -webkit-sticky;
  position: sticky;
  -webkit-flex: 0.2 1;
          flex: 0.2 1;
  border-radius: 20px;
  text-align: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

.sidebar__avatar {
  margin-bottom: 10px;
}

.sidebar__top {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background-color: white;
  padding-bottom: 10px;
}
.sidebar__top > h4 {
  color: gray;
  font-size: 11px;
}
.sidebar__top > h2 {
  font-size: 18px;
}

.sidebar__top > img {
  margin-bottom: -20px;
  width: 100%;
  height: 60px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
}
.sidebar__stat {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 10px;
}
.sidebar__stat > p {
  color: gray;
  font-size: 11px;
  font-weight: 600;
}
.sidebar__statNumber {
  font-weight: bold;
  color: rgb(109, 109, 252) !important;
}

.sidebar__stats {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid lightgray;
  background-color: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.sidebar__bottom {
  text-align: left;
  padding: 10px;
  border: 1px solid lightgray;
  background-color: white;
  border-radius: 10px;
  margin-top: 10px;
}
.sidebar__bottom > p {
  font-size: 13px;
  padding-bottom: 10px;
}

.sidebar__recentItem {
  display: -webkit-flex;
  display: flex;
  font-size: 11px;
  color: gray;
  font-weight: bolder;
  cursor: pointer;
  margin-bottom: 5px;
  padding: 5px;
}

.sidebar__recentItem:hover {
  color: black;
  background-color: whitesmoke;
  border-radius: 10px;
  cursor: pointer;
}

.sidebar__hash {
  margin-right: 10px;
  margin-left: 5px;
}

.feed {
  -webkit-flex: 0.6 1;
          flex: 0.6 1;
  margin: 0 20px;
}

.feed__inputContainer {
  /* display: flex;
  justify-content: space-evenly; */
  background-color: white;
  padding: 10px;
  padding-bottom: 20px;
  border-radius: 10px;
  margin-bottom: 20px;
}
.feed__input {
  border: 1px solid lightgray;
  border-radius: 30px;
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  color: gray;
  padding-left: 15px;
}

.feed__input > form {
  display: -webkit-flex;
  display: flex;
  width: 100%;
}

.feed__input > form > input {
  border: none;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-left: 10px;
  outline-width: 0;
  font-weight: 600;
}

.feed__input > form > button {
  display: none;
}
.feed__inputOptions {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-evenly;
          justify-content: space-evenly;
}



.inputOption {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-top: 15px;
  color: gray;
  padding: 10px;
  cursor: pointer;
}
.inputOption:hover {
  background-color: whitesmoke;
  border-radius: 10px;
}

.inputOption > h4 {
  margin-left: 5px;
}

.post {
  background-color: white;
  padding: 15px;
  margin-bottom: 10px;
  border-radius: 10px;
}

.post__header {
  display: -webkit-flex;
  display: flex;
  margin-bottom: 10px;
}

.post__info {
  margin-left: 10px;
}

.post__info > p {
  font-size: 11px;
  color: gray;
}
.post__body {
  overflow-wrap: anywhere;
}
.post__button{
  display: -webkit-flex;
  display: flex;
}


.login {
  display: grid;
  place-items: center;
  padding-top: 100px;
  padding-bottom: 100px;
  margin-left: auto;
  margin-right: auto;
}

.login > img {
  object-fit: contain;
  height: 70px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.login > form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.login > form > input {
  width: 350px;
  height: 50px;
  font-size: 20px;
  padding-left: 10px;
  margin-bottom: 10px;
  border-radius: 15px;
}
.login > form > button {
  width: 365px;
  height: 35px;
  font-size: large;
  background-color: #d052fc;
  border-radius: 10px;
}

.login > p {
  color: black;
}

.login__register {
  cursor: pointer;
  color: #d052fc;
}

.widgets {
  position: -webkit-sticky;
  position: sticky;
  top: 80px;
  -webkit-flex: 0.2 1;
          flex: 0.2 1;
  background-color: white;
  border-radius: 10px;
  border: 1px solid lightgray;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding-bottom: 10px;
}
.widgets__header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 10px;
  color: #a442c7;
}

.widgets__header > h2 {
  font-size: 16px;
  font-weight: 400;
}

.widgets__article {
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  padding: 10px;
}
.widgets__article:hover {
  background-color: whitesmoke;
}
.widgets__articleLeft {
  color: #d052fc;
  margin-right: 10px;
}
.widgets__articleLeft > .MuiSvgIcon-root {
  font-size: 10px;
}

.widgets__articleRight {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.widgets__articleRight > h4 {
  font-size: 14px;
}

.widgets__articleRight > p {
  font-size: 11px;
  color: gray;
}

.widgets__friend {
  display: -webkit-flex;
  display: flex;
  cursor: pointer;
  padding: 10px;
}
.widgets__friend:hover {
  background-color: whitesmoke;
}
.widgets__friendLeft {
  color: #d052fc;
  margin-right: 10px;
}
.widgets__friendLeft > .MuiSvgIcon-root {
  font-size: 10px;
}

.widgets__friendRight {
  -webkit-flex: 1 1;
          flex: 1 1;
}

.widgets__friendRight > h4 {
  font-size: 14px;
}

.widgets__friendRight > p {
  font-size: 11px;
  color: gray;
}
.widgets__friendHeader {
  color: #a442c7;
  font-size: medium;
  padding-left: 10px;
}

